/*------------------------------------------------------------------
2.0 - General Behaviour
------------------------------------------------------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none !important;
  font-family: sans-serif; }
  *::-webkit-scrollbar {
    width: 5px; }
  *::-webkit-scrollbar-thumb {
    background: #666; }

body {
  display: grid;
  grid-template-areas: "iconContainer statusBar windowButtons" "nav main main";
  grid-template-columns: 60px auto 120px;
  grid-template-rows: 40px auto;
  height: 100vh;
  background-color: #273136; }

h1 {
  color: #d3d4d4;
  text-align: center;
  grid-area: pageTitle; }

h2 {
  color: #ffb270;
  margin: 20px 0px 10px 0px; }

h3 {
  color: #ffed72;
  margin-top: 10px;
  margin-left: 20px; }

h3:not(:first-of-type) {
  margin-top: 15px; }

h4 {
  color: white;
  margin-top: 15px;
  margin-left: 30px; }

a {
  color: white;
  text-decoration: none; }

button {
  border: none;
  cursor: pointer; }

input[type=color] {
  border: none;
  background: none;
  cursor: pointer; }

/*------------------------------------------------------------------
3.0 - Icon
------------------------------------------------------------------*/
#iconContainer {
  grid-area: iconContainer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1c2528; }
  #iconContainer img {
    width: 20px;
    height: auto; }

/*------------------------------------------------------------------
4.0 - Title Bar
------------------------------------------------------------------*/
#statusBar {
  grid-area: statusBar;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #1c2528;
  color: grey;
  font-size: 14px;
  -webkit-user-select: none;
  -webkit-app-region: drag; }

/*------------------------------------------------------------------
5.0 - Window Menu
------------------------------------------------------------------*/
#windowButtons {
  grid-area: windowButtons;
  display: flex;
  background-color: #1c2528; }
  #windowButtons .windowButton {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 0 auto;
    border: none;
    background: none;
    color: grey;
    cursor: pointer; }
  #windowButtons #fullScreenButton {
    font-size: 19px; }
  #windowButtons #fullScreenButton:hover,
  #windowButtons #minimizeButton:hover {
    background-color: #343b3e; }
  #windowButtons #closeButton:hover {
    background-color: #d41324;
    color: white; }

/*------------------------------------------------------------------
6.0 - Navigation Menu
------------------------------------------------------------------*/
nav {
  grid-area: nav;
  height: calc(100vh - 40px);
  background-color: #161b1e; }
  nav .nav_item {
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background: none;
    color: #545f62;
    font-size: 20px;
    cursor: pointer; }
    nav .nav_item.active {
      color: #b8c4c3; }
  nav .nav_item:hover {
    color: #b8c4c3; }

/*------------------------------------------------------------------
7.0 - Dynamic Content
------------------------------------------------------------------*/
#dynamicContent {
  grid-area: main;
  height: calc(100vh - 40px);
  padding: 30px 20px;
  overflow: auto;
  position: relative;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto; }
  #dynamicContent #pageContent {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto 1fr;
    grid-template-areas: "pageTitle" "content"; }
  #dynamicContent #actionPopup {
    position: fixed;
    top: 40px;
    right: 0;
    transform: translateX(100%);
    opacity: 0;
    width: 250px;
    max-width: 50%;
    padding: 10px;
    background-color: #3a4449;
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
    transition: all .2s ease-in-out; }
    #dynamicContent #actionPopup.showActionPopup {
      transform: translateX(0px) !important;
      opacity: 1 !important; }
    #dynamicContent #actionPopup #popupTitle {
      font-size: 14px;
      font-weight: bold;
      color: white; }
    #dynamicContent #actionPopup #popupMessage {
      font-size: 12px;
      font-weight: normal;
      color: whitesmoke;
      margin-top: 8px; }
